@import url(https://fonts.googleapis.com/css2?family=Lakki+Reddy&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'Lakki Reddy', cursive;
}

.color {

}
/* shared */
.text-center {
  text-align: center;
}

/* Nav */

.navContainer {
  display: flex;
  width: 100%;
  justify-content: center;
}

.navLink {
  flex-basis: calc(100% / 3 - 10px);
  color: lemonchiffon;
  text-decoration: none;
  border-radius: 20px;
  border: 4px solid black;
  background: #11810d;
  background: linear-gradient(to bottom right, #11810d, #2e44ce);
}

.active-link {
  color: aqua;
}

.navContainer:hover {
  cursor: pointer;
}

/* containers */

.container {
  display: flex;
}

.form-container {
  flex-basis: calc(100% / 3 - 8px);
  border: 2px solid black;
  display: block;
  width: 365px;
  margin: auto;

}

.drink-container {
  flex-basis: calc(100% / 2 - 10px);
  margin: 5px;
  flex-direction: column;
}

/* colors */

body {
  background-color: rgb(0, 0, 0);
  background-image: url(http://www.zingerbugimages.com/backgrounds/tan_parchment_paper_wallpaper_texture_seamless.jpg);
  background-position: top left;
  background-repeat: repeat;
  background-attachment: fixed;
}

.background-everything {
  background-color: rgb(0, 0, 0);
  background-image: url(http://www.zingerbugimages.com/backgrounds/tan_parchment_paper_wallpaper_texture_seamless.jpg);
  background-position: top left;
  background-repeat: repeat;
  background-attachment: fixed;
}

button {
  background-color: cadetblue;
  border-radius: 20px;
  border: 1px solid black;
  color: lemonchiffon;
  margin:auto;
}

/* images */

.bk-img {
  position: relative;
  height: 750px;
  align-items: center;
  justify-content: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  opacity: 60%;
}

.img-gif {
  width: 300px;
}

.img-drink {
  width: 350px;
}

.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
}



